      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">polyline1</span>: {},
        <span class="hljs-attr">positions1</span>: [{ <span class="hljs-attr">lng</span>: <span class="hljs-number">90</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">20</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">10000</span> }, { <span class="hljs-attr">lng</span>: <span class="hljs-number">120</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">20</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">10000</span> }],
        <span class="hljs-attr">material1</span>: <span class="hljs-literal">undefined</span>,
        <span class="hljs-attr">polyline2</span>: {},
        <span class="hljs-attr">positions2</span>: [{ <span class="hljs-attr">lng</span>: <span class="hljs-number">90</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">30</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">10000</span> }, { <span class="hljs-attr">lng</span>: <span class="hljs-number">120</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">30</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">10000</span> }],
