            }
          }
        })
        <span class="hljs-variable language_">this</span>.<span class="hljs-property">material2</span> = <span class="hljs-keyword">new</span> <span class="hljs-title class_">Cesium</span>.<span class="hljs-title class_">Material</span>({
          <span class="hljs-attr">fabric</span>: {
            <span class="hljs-attr">type</span>: <span class="hljs-string">'PolylineGlow'</span>,
