<h2>属性</h2>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>Boolean</td>
<td>true</td>
<td><code v-pre="">optional</code> 指定是否显示折线。</td>
</tr>
<tr>
<td>width</td>
<td>Number</td>
<td>1.0</td>
<td><code v-pre="">optional</code> 指定折线宽度。</td>
</tr>
<tr>
<td>loop</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 指定折线是否首尾相连。</td>
</tr>
<tr>
<td>material</td>
<td>Object</td>
<td>Material.ColorType</td>
<td><code v-pre="">optional</code> 指定折线材质。</td>
</tr>
<tr>
<td>positions</td>
<td>Array</td>
<td></td>
<td><code v-pre="">optional</code> 指定线位置数组。</td>
</tr>
<tr>
<td>id</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定折线被选中时的返回值。</td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 根据相机的距离确定折线是否可见。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档：<strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/Polyline.html">Polyline</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>mousedown</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标在该图元上按下时触发。</td>
</tr>
<tr>
<td>mouseup</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标在该图元上弹起时触发。</td>
</tr>
<tr>
<td>click</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标单击该图元时触发。</td>
</tr>
<tr>
<td>clickout</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标单击该图元外部时触。</td>
</tr>
<tr>
<td>dblclick</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标左键双击该图元时触发。</td>
</tr>
<tr>
<td>mousemove</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标在该图元上移动时触发。</td>
</tr>
<tr>
<td>mouseover</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标移动到该图元时触发。</td>
</tr>
<tr>
<td>mouseout</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标移出该图元时触发。</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          polyline1: {},
          positions1: [
            { lng: 90, lat: 20, height: 10000 },
            { lng: 120, lat: 20, height: 10000 }
          ],
          material1: undefined,
          polyline2: {},
          positions2: [
            { lng: 90, lat: 30, height: 10000 },
            { lng: 120, lat: 30, height: 10000 }
          ],
          material2: undefined,
          polyline3: {},
          positions3: [
            { lng: 90, lat: 40, height: 10000 },
            { lng: 120, lat: 40, height: 10000 }
          ],
          material3: undefined,
          polylines: []
        }
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
          this.material1 = new Cesium.Material({
            fabric: {
              type: 'Color',
              uniforms: {
                color: Cesium.Color.RED
              }
            }
          })
          this.material2 = new Cesium.Material({
            fabric: {
              type: 'PolylineGlow',
              uniforms: {
                color: Cesium.Color.BLUE
              }
            }
          })
          this.material3 = new Cesium.Material({
            fabric: {
              type: 'PolylineArrow',
              uniforms: {
                color: Cesium.Color.PURPLE
              }
            }
          })
        },
        clicked (e) {
          console.log(e)
        }
      }
    }
  </script>